import 'typeface-merriweather';

const chineseSanSerifFonts = [
  '"PingFang SC"', // , <mac>
  '"Hiragino Sans GB"', // , <mac>
  '"Microsoft YaHei"', // , win
  '"Heiti SC"', // -, win
  '"WenQuanYi Micro Hei"', // <Linux>
];
const chineseSerifFonts = [
  '"Noto Serif CJK SC"',
  '"Source Han Serif SC"',
  '"Source Han Serif CN"', // 
  '"Songti SC"', // , mac
  '"SimSun"', // , win
  '"STSong"', // 
  '"AR PL Sungti"', // , linux
];

const serif = `"Merriweather", Georgia, ${chineseSerifFonts}, serif`;
const sansSerif = `"SF Pro Display", "-apple-system", "BlinkMacSystemFont", "San Francisco", "Helvetica Neue", "Helvetica", "Ubuntu", "Roboto", "Noto", "Segoe UI", "Arial", ${chineseSanSerifFonts.join(
  ' ,'
)}, sans-serif`;

export default {
  serif,
  sansSerif,
  body: sansSerif,
  heading: serif,
  monospace: `"Dank Mono", "Noto Sans Mono", "Menlo", "Roboto Mono", "Consolas", "Operator Mono", "Monaco", "source-code-pro", "Courier New", ${sansSerif}, monospace`,
};
